import React from "react";
import { Navbar,Container,Nav,NavDropdown } from 'react-bootstrap';
import { Link } from 'react-router-dom';
const NavBar =  () => {

    return(
<Navbar bg="light" expand="lg">
  <Container>
    <Navbar.Brand href="#home">Rafi's warehouse</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="me-auto">
        <Nav.Link href="/">הוסף מוצר לזיכוי</Nav.Link>
        <Nav.Link  href="/add_provider">הוסף ספק</Nav.Link>
        <NavDropdown title="More Actions" id="basic-nav-dropdown">
          <NavDropdown.Item href="/last_month">צפה בחודש האחרון</NavDropdown.Item>
          <NavDropdown.Item href="/info_provider">מידע לפי ספק</NavDropdown.Item>
          <NavDropdown.Item href="/export_csv">ייצוא דוח דינמי לאקסל</NavDropdown.Item>
        </NavDropdown>
      </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>
    )



}
export default NavBar