import React,{useEffect,useState} from 'react'
import Axios from '../../helpers/axios'
import { Table,Button ,Col,Container,Row} from 'react-bootstrap';

const LastMonth = () => {
const[data,setData] = useState(null)

useEffect(() => {
        (async function() {
            try {
                const {data:data} = await Axios.get("/get_last_month")
                setData(data);
            } catch (e) {
                console.error(e);
            }
        })();
    },[]);
    const ths = ["תאריך","המוצר","הספק","עוד מידע","מחיר"]
 
    return(
        <Table className ="table">
            <thead>
            <tr>
                {ths.map(th => <th  scope="col" className="border-1">{th}</th>)}
            </tr>
            </thead>
            <tbody>
            {data.map(item =>{
                return (
                <tr>    
                <td  scope="col" className="border-1">{item.provider}</td>
                <td  scope="col" className="border-1">{item.product}</td>
                <td  scope="col" className="border-1">{item.info}</td>
                <td  scope="col" className="border-1">{item.date}</td>
                </tr>
                 )   
                })
            }
            </tbody>
        </Table>)
}

export default LastMonth